<template>
	<div class="search-user">
		<v-col class="search-input-form">
			<v-text-field label="name"
						  hint="istifadecinin adn daxil edin"
						  :error="errorText||false"
						  :error-messages="errorText"
						  filled
						  @change="onChange"
						  v-model="name"
						  :loading="loading"
						  :disabled="loading"/>
		</v-col>
		<v-col cols="12"
			   sm="12"
			   md="12"
			   class="users"
			   v-if="users.length">
			<v-list
				max-width="800"
			>
				<v-list-item v-for="user in users" dense selectable style="border-bottom: 1px solid gray;">
					<v-list-item-content style="position:relative;">
						<v-list-item-title>{{getFirstname(user)}}</v-list-item-title>
						<div class="d-flex flex-wrap pa-4">
							<v-chip :color="user.quiz1 === 'CONFIRMED'? 'success' : ''">xsiyytin profili</v-chip>
							<v-chip :color="user.quiz2 === 'CONFIRMED'? 'success' : ''">Dyrlr icmal</v-chip>
							<v-chip :color="user.quiz3 === 'CONFIRMED'? 'success' : ''">Tlbatlar</v-chip>
							<v-chip :color="user.quiz4 === 'CONFIRMED'? 'success' : ''">Frustrasiya</v-chip>
							<v-chip :color="user.quiz5 === 'CONFIRMED'? 'success' : ''">Aqressiya</v-chip>
						</div>
						<v-btn
							absolute
							dark
							fab
							x-small
							right
							@click="selectUser(user)"
							color="primary"
						>
							<v-icon>mdi-check</v-icon>
						</v-btn>
					</v-list-item-content>
					<hr>
				</v-list-item>
			</v-list>
		</v-col>

	</div>
</template>

<script>
	import {dataStore} from "../firebase";

	export default {
		name: "Manage",
		data() {
			return {
				name: "",
				errorText: "",
				loading: false,
				users: []
			}
		},
		methods: {
			getFirstname: (user) => {
				if (user.personalData) {
					return `${user.personalData.firstname} ${user.personalData.lastname}`
				} else {
					return 'No data'
				}
			},
			onChange: function () {
				const self = this;
				this.errorText = "";
				this.loading = true;
				dataStore
					.collection('results')
					.where("personalData.firstname", "==", self.name)
					.get()
					.then(function (querySnapshot) {
						if (querySnapshot.empty) {
							self.errorText = 'Mlumat taplmad'
						}
						const __users = [];
						querySnapshot.forEach(function (doc) {
							// doc.data() is never undefined for query doc snapshots
							__users.push(doc.data());
						});
						self.users = __users;
						console.log("users => ", __users);
					})
					.catch(function (error) {
						console.log("Error getting documents: ", error);
						self.errorText = error.message
					})
					.finally(function () {
						self.loading = false;
					});
			},
			selectUser: function (user) {
				console.log("user -> ", user);
				this.$cookies.set("userId", user.personalData.id);
				this.$store.dispatch("setUser", user);
				this.$router.push({name: "personalinfo"});
			}
		}
	}
</script>

<style scoped lang="scss">
	.search-user {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;

		.search-input-form {
			width: 50%;
		}

		.users {
			display: flex;
			justify-content: center;
		}
	}
</style>
