<template>
	<v-container>
		<v-layout>
			<v-sheet elevation="1" max-width="100%">
				<v-col cols="12" class="pa-0">
					<v-banner sticky class="banner text-center" min-width="100%" elevation="10" dark>
						<p class="banner-text display-1 font-weight-bold text-center">XSYYTN PROFL</p>
					</v-banner>
					<!--					<v-divider class="my-6"/>-->
					<v-banner style="background-color: #E8470C" class="banner d-flex justify-center" min-width="100%"
							  elevation="10">
						<template>
							<p class="mb-0 paragraph-fill headline font-weight-bold text-center">
								TLMAT: Aada bzi insanlarn tsviri verilmidir. Zhmt olmasa, vvlc hr bir
								tsviri oxuyun
								v hr insann siz n qdr oxamasn v ya oxamamasn fikirlin. Solda tsvir
								olunan insann siz n qdr oxamasn sa trfd qeyd edin.
							</p>
						</template>
					</v-banner>
					<v-divider class="my-1"/>
					<v-banner dark elevatio="12"
							  class="question-sheet">
						<v-sheet elevation="0" class="table-header">
							<v-row class="table-row">
								<v-col cols="5"/>
								<v-col cols="7">
									<v-row no-gutters>
										<v-col style="width: 20%" class="text-center"
											   v-for="k in schwartzQuizOpinionTypes" :key="k.id">
											{{k.title}}
										</v-col>
									</v-row>
								</v-col>
							</v-row>
						</v-sheet>
						<v-sheet elevation="0" class="table-body">
							<QuestionType2
								:title="q.label"
								v-for="(q,i) in schwartzQuiz"
								:id="q.id"
								:key="q.id"
								:error="errors[i]"
								:types="schwartzQuizOpinionTypes"
								:test="test"
								@handleError="handleError"
								@handleResult="handleResult"/>
						</v-sheet>
					</v-banner>
					<v-row justify="center" align-content="center" no-gutters>
						<v-btn elevation="12"
							   width="50%"
							   min-height="60"
							   class="my-5 align-self-start"
							   type="submit"
							   color="primary"
							   @click="handleSubmit"
							   large
							   outlined>
							Nvbti
							<v-icon>mdi-chevron-right</v-icon>
						</v-btn>
					</v-row>
				</v-col>
			</v-sheet>
		</v-layout>
	</v-container>
</template>

<script>
	import {form_not_valid_message, user_does_not_exist_message} from "./messages";
	import {quiz_completed_message} from "./messages";
	import {dataStore} from "../firebase";
	import {schwartzQuiz2, schwartzQuizOpinionTypes} from "./Statics"
	import QuestionType2 from "./QuestionType2";
	import {mapState} from "vuex";

	export default {
		name: "Schwartz2",
		components: {QuestionType2},
		created() {
			this.errors = this.schwartzQuiz.filter(() => false);
			this.toast = this.$swal.mixin({
				toast: true,
				position: 'top-end',
				type: 'error',
				showConfirmButton: false,
				timer: 3000,
				showCloseButton: true,
				padding: '10px',
				onOpen: (toast) => {
					toast.addEventListener('mouseenter', this.$swal.stopTimer);
					toast.addEventListener('mouseleave', this.$swal.resumeTimer)
				}
			});
		},
		data: () => ({
			opinion: true,
			schwartzQuiz: schwartzQuiz2,
			errors: [],
			schwartzQuizOpinionTypes,
			test: "beledi"
		}),
		mounted() {
			if (this.user === null) {
				alert('No user');
				this.$router.replace('/')
			}
			this.$store.dispatch("setActiveStepper", this.$route.meta.index);
		},
		methods: {
			handleError(id, status) {
				const errs = this.errors;
				errs[id] = status;
				this.errors = errs;
				this.test = Math.random(0, 1).toString();
			},
			handleResult(id, opinion) {
				this.schwartzQuiz.filter((q, i) => {
					if (q.id === id) {
						this.schwartzQuiz[i].opinion = this.schwartzQuizOpinionTypes[opinion].id;
					}
				});

			},
			handleSubmit() {
				this.test = "deyisdi";
				const valid = this.isValid();
				if (valid) {

					debugger;
					if (this.user && this.user.personalData && this.user.personaldata === "CONFIRMED") {
						if (this.user.schwartzResults)
							this.user.schwartzResults.part2 = this.schwartzQuiz;
						else
							this.user.schwartzResults = {
								part2: this.schwartzQuiz
							};
						this.user.quiz2 = true;
						dataStore
							.collection("results")
							.doc(this.user.personalData.id)
							.set(this.user)
							.then(() => {
								this.$store.dispatch("setUser", this.user);
								this.toast.fire({
									type: 'success',
									title: quiz_completed_message
								});
								this.$router.push({name: "needs"});
							});
					} else {
						this.$swal({
							title: user_does_not_exist_title,
							text: user_does_not_exist_message,
							type: "error",
							showCancelButton: true,
							showConfirmButton: false,
							cancelButtonText: "OK"
						});
					}
				} else {
					this.toast.fire({
						type: 'error',
						title: form_not_valid_message
					})
				}
			},
			isValid() {
				let valid = true;
				this.schwartzQuiz.filter((q, i) => {
					if (q.opinion === null || q.opinion === undefined || q.opinion === '') {
						this.errors[i] = true;
						valid = false;
					}
				});
				return valid
			}
		},
		computed: {
			...mapState(["user"])
		},
	}
</script>

<style scoped lang="scss">
	.v-input__control {
		width: 100% !important
	}

	.table-header {
		.table-row {
			width: 100%;

			.table-col {
				width: 20%;
			}
		}

	}

	.table-body {
		width: 100%;
	}

</style>
